<template>
  <div class="addQuotation">
    <div class="container">
      <van-cell-group :border="false">
        <van-field
          v-model="EventsData.companyName"
          label=""
          type="text"
          input-align="right"
          placeholder=""
          :error="false"
          :border="false"
          :readonly="true"
        />

        <div class="select-list">
          <div class="list">
            <div class="left">
              
            </div>
            <div class="right">
              <select
                v-model="EventsData.projectName"
                v-placeholder="{color: EventsData.projectName}"
                :disabled="OTP"
              >
                <option
                  v-for="(itm,idx) in pgList"
                  :key="idx"
                  :value="itm.information.projectName"
                >
                  {{ itm.information.projectName }}
                </option>
              </select>
            </div>
            <div
              v-sanjiao="{color: EventsData.projectName}"
              class="saojiao"
            />
          </div>
        </div>
        <div class="money-text">
          <van-field
            v-model="EventsData.draftQuotation"
            label=""
            style="width:90%;"
            type="number"
            input-align="right"            
            placeholder=" :"
            :error="false"
            :border="false"
            :readonly="upType==2"
          />
          <span class="right-text"></span>
        </div>

        <van-field
          v-model="EventsData.remark"
          label=""
          type="textarea"
          input-align="right"
          placeholder=""
          rows="1"
          autosize
          :border="false"
          :readonly="upType==2"
        />

        <div
          v-if="upType!=2"
          class="select-map"
        >
          <div class="uploadFile">
            <span class="iconfont iconshangchuan uploadIcon" />
            <input
              class="file"
              name="file"
              type="file"
              @change="update"
            >
            <div class="uploadText">
              
            </div>
          </div>
        </div>
        <div style="width: 50%;overflow: hidden;margin-left: 10px;">
          {{ newFile }}
        </div>
        <div
          v-if="upType!=2"
          class="select-list"
        >
          <div
            v-if="workType!=3"
            class="list"
          >
            <div class="left">
              
            </div>
            <div class="right">
              <select
                v-model="EventsData.visitId"
                v-placeholder="{color: EventsData.visitId}"
                :disabled="OTP"
              >
                <option value="">
                  
                </option>
                <option value="1">
                  
                </option>                
              </select>
            </div>
            <div
              v-sanjiao="{color: EventsData.visitId}"
              class="saojiao"
            />
          </div>
        </div>
        <add-visit
          v-if="EventsData.visitId==1&&upType!=2"
          :is-c-d-t="isCDT"
          :is-c-p-t="true"
          @VisitChange="watchVisit"
        />
      </van-cell-group>

      <div class="footer">
        <van-button
          v-if="upType!=2"
          class="gradual-btn"
          style="width:100%;"
          type="primary"
          @click="addSet"
        >
          
        </van-button>
      </div>
    </div>
  </div>
</template>
<script>

import { mapState } from 'vuex'
import { formatDate  } from '@/utils'
import { addProject, upload, projectList, idProject } from '@/service/customer'
import { request } from 'http'
import addVisit from '../addVisit/addVisit.vue'
export default {
  name: 'AddQuotation',
  // components: {
  //   'add-visit': addVisit,
  // },
  //workType:1,;2,;3EvaentsData
  props: {
    workType: {
      type: Number,
      default: null
    },
    upIng: {
      type: Boolean,
      default: false
    },
    //
    isNo:{
      type:Number ,
    },
  },
  data() {
    return {
      project: -1, // 
      quotation: '', // 
      memo: '', // 
      visitIndex: -1, // 
      EventsData: {
        projectName: null, // 
        draftQuotation: '', // 
        remark: '', // 
        attachmentAddress: '', // url
        visitId: '', // 
        projectId: '',
        
      },
      visitDTO:null,
      pgList: [],
      upType: 1,
      isCDT:false,//isCallDataType
      newFile:'',
      OTP:false,//
    }
  },
  computed: {
    ...mapState(['companyName']),

  },
  watch:{
    upIng(newV,oldV){
      console.log('upIng',newV,oldV)
      this.pgList.map(d=>{
        this.EventsData.projectId = d.information.projectName == this.EventsData.projectName ? d.id: this.EventsData.projectId
      })
      let params = {
        information:JSON.stringify(this.EventsData),
        "menuType":2,
		    "companyId":this.$route.query.id,
		    "projectId":this.EventsData.projectId,
		    "userId":this.$store.getters.userInfo.id,
        "userName":this.$store.getters.userInfo.name,
        isNo:this.isNo
      }
      let txts = {                                
        draftQuotation:'',        
      }       
      if (!this.$checkFrom(this.EventsData,txts)) {
        return
      }
      this.$emit('upedChange',params)
      
      return
    },
  },
  mounted() { },

  created() {
     this.getProjectList()
    if (this.$route.query.id) {
      this.getIdProject(this.$route.query.id)
      this.upType = 2
      this.OTP =  true 
    }
   
    this.EventsData.companyName = this.$store.getters.userData.companyName
    this.EventsData.companyId = this.$store.getters.userData.companyId
  },
  methods: {
    //
    formCheck(){
      let params = this.lendItemD.lendingItem
      let txts = {                                
        lendAmount:'',
        lendTime:'',        
      } 
      if (!this.$checkFrom(params,txts)) {
        return
      }
    },
    //
    watchVisit(e){
      console.log('',e)
      this.visitDTO = e
      this.clickAddEvents()
    },
    //id
    getIdProject(id) {
      idProject({ id: id }).then(res => {
        // console.log('idProject',res)
        this.EventsData = res.information
      })
    },
    callBack() {
      this.$emit('addCall', this.EventsData)
    },    
    getProjectList() {
      const params = {
        menuType: 1,
        size:1000
      }
      projectList(params).then((res) => {
        console.log('projectList', res)
        this.pgList = res.data
        if(res.data.length>0){
          if(this.EventsData.projectName){
            return
          }
          this.EventsData.projectName = res.data[0].information.projectName
        }
        
      })
    },
    //
    addSet(){      
      if(this.EventsData.visitId==1){
        this.isCDT = !this.isCDT
      }else{
        this.clickAddEvents()
      }
    },
    clickAddEvents() {
      
      // return 
      const EventsData = {}
      this.pgList.map(d => {
        this.EventsData.projectId = d.information.projectName == this.EventsData.projectName ? d.id : this.EventsData.projectId
      })
      EventsData.information = JSON.stringify(this.EventsData)
      EventsData.menuType = 2
      EventsData.projectId = this.EventsData.projectId
      EventsData.visitDTO = this.visitDTO
      // console.log('EventsData',EventsData)
      let txts = {                                
        draftQuotation:'',        
      }       
      if (!this.$checkFrom(this.EventsData,txts)) {
        return
      }
      addProject(EventsData).then(res=>{
        this.$router.go(-1)
      })
    },    
    update(e) {
      let file = e.target.files[0]
      let _this = this
      let param = new FormData()
      param.append('file', file)
      let userData = this.$store.getters.userData
      let fileName = userData.companyAbbreviation+'-'+userData.companyBusinessLicense+'-'+ formatDate(new Date(),'yyyyMMdd')
      let config = {
        headers: { 'Content-Type': 'multipart/form-data' }
      }
      upload(param, config)
        .then(res => {
          // console.log('', res.data,_this.$notifySty)
          // console.log();
          this.$notifySty('')
          this.newFile = res.data.url
          this.EventsData.fileName = fileName
          this.EventsData.fileUrl = res.data.url
        })
    }
  },
}
</script>
